<template>
    <div class="main">
    	<div class="title"><img src="/img/back_2.png" @click="back">查看评价</div>
        <div class="eval_item" v-for="item in evaluatelList">
            <div class="customer">
                <img :src="item.img" alt="">
                <span class="name">{{item.name}}</span>
                <span class="type">{{item.type}}</span>
                <span class="date">{{item.date}}</span>
            </div>
            <div class="eval_content">
                {{item.text}}
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            evaluatelList: []
        }
    },
    created: function() {
        for (var i = 0; i < 10; i++) {
            this.evaluatelList.push({
                img: "/img/head.png",
                name: "用户名称",
                type: "种类1",
                text: "产品不错",
                date: "2017-09-15"
            })
        }
    },
    methods: {
    	back: function(){
    		history.go(-1)
    	}
    }
}
</script>
<style lang="scss">
* {
    margin: 0;
    padding: 0;
}

.main {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
    padding-top: 45px;
}
.title{
	width: 100%;
	height: 45px;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	position: fixed;
	top: 0;
	max-width: 640px;
	margin: 0 auto;
	background-color: #fff;
	img{
		width: 30px;
		height: 30px;
		margin-right: 15px;
	}
	color: #939;
	
}
.eval_item {
    width: 95%;
    margin: 0 auto;
    padding: 25px 0;
    .customer {
        width: 100%;
        height: 30px;
        line-height: 30px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        padding-left: 3%;
        padding-bottom: 15px;
        border-bottom: 1px solid #fafafa;
        img {
            width: 30px;
            height: 30px;
            border-radius: 50%;
        }
        .name {
            margin-left: 8px;
            color: #666;
            display: inline-block;
            width: 20%;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
    }
    .type {
        color: #aaa!important;
        font-size: 0.8rem;
        margin-left: 2%;
    }
    .date{
    	color: #aaa;
    	font-size: 0.8rem;
    	margin-left: 2%;
    }
    .eval_content {
        width: 95%;
        margin: 0 auto;
        color: #666;
        font-size: 0.9rem;
        box-sizing: border-box;
        padding: 5px 15px;
    }
}
</style>


